@import url('https://fonts.googleapis.com/css?family=Lato|Poppins');
@import url('https://fonts.googleapis.com/css?family=Sedgwick+Ave|Sue+Ellen+Francisco');

@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

@font-face {
  font-family: 'Galano';
  src: url('../../../../assets/fonts/Galano Grotesque DEMO Bold.otf');
}

.album {
    border-radius: 5px;
    width: 200px;
    height: 200px;
    margin-top: 40px;
    -webkit-box-shadow: 0 20px 40px rgba(0,0,0,0.45), 0 6px 6px rgba(0,0,0,0.25);
            box-shadow: 0 20px 40px rgba(0,0,0,0.45), 0 6px 6px rgba(0,0,0,0.25);

    -webkit-transition: margin-top 0.5s, width 0.5s, height 0.5s;

    -o-transition: margin-top 0.5s, width 0.5s, height 0.5s;

    transition: margin-top 0.5s, width 0.5s, height 0.5s;
    -webkit-transition-timing-function: ease;
         -o-transition-timing-function: ease;
            transition-timing-function: ease;
}

.album:hover {
    margin-top: 10px;
}

.nextArrow {
	display: block;
	top: 25%;
	width: 40px;
	height: 40px;
	right: -45px;
}

.prevArrow {
	display: block;
	top: 25%;
	width: 40px;
	height: 40px;
	left: -45px;
}

.nextArrow:before, .prevArrow:before {
	font-size: 60px;
	color: #7a21ff;
}



